.different-banner {
  position: relative;
  min-width: 18rem;
  height: 14rem;
  max-height: 14rem;
  background-color: rgb(0, 88, 88);
  border-radius: 5px;
}
.bg-different-banner {
  background-color: #ffffff;
  height: 100%;
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
  border-radius: 5px;
}
.info-different-banner {
  position: relative;
  background-color: #05050549;
  width: 100%;
  height: 100%;
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  justify-content: center;
  padding: 0rem 2.9rem 0rem 2.9rem;
  box-sizing: border-box;
  z-index: 999999;
  font-size: 1rem;
  backdrop-filter: blur(10px);
  -webkit-backdrop-filter: blur(10px);
  border-radius: 5px;
}
.header-card {
  width: 100%;
  display: flex;
  justify-content: space-between;
}

.subtitle-banner {
  font-size: 0.8em;
  color: #ffffff81;
  margin-bottom: 0.2rem;
}
.published {
  font-size: 0.7em;
  color: #ffffff81;
}
.title-banner {
  font-size: 1.7em;
  color: #ffffff;
}
.text-banner {
  font-size: 0.8em;
  margin-bottom: 1.9rem;
  margin-top: 0.6rem;
  text-align: start;
  color: #ffffffa8;
}
.container-buttons-banner {
    display: flex;
}
.container-buttons-banner > div {
    margin-right: 1.3rem;
}
